@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Nanum Gothic', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root {
  display: flex;
  justify-content: center;
  margin-top: 20px;
} 
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.primary-btn{
background-color: #4f4f4f;
border-color: #4f4f4f;
}

.secondary-btn{
background-color: #b2b2b2;
border-color: #b2b2b2;
color: black;
}

.profile-pic{
border-radius: 40px;
margin-left: 20px;
margin-right: 15px;
}

.header-modalbutton{
    border: 0px;
    background: rgb(38, 51, 67);
    font-size: 24px;
    margin-left: 30px;
    color: #007bff
}

.genre{
margin: 10px;
}

.people-number{
margin: 10px;
}
.HD-header {
    background-color: white;
    width: 1000px;
    margin-top: 10px;
}
.HD-navbar {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: white;
}
.Hd-link {
    float: left;
    background-color: white;
    color: black;
    display: block;
    text-align: center;
    padding: 10px;
    padding-bottom: 0px;
    margin-top: 18px;
    font-size: 1.3em;
}
.HD-la {
    margin-right: 100px;
}
.HD-logo {
    font-size: 2em;
    margin: 10px;
    margin-right: 20px;
    font-weight: 900;
    color: #43afc7;
}
.Hd-link:hover {
    color: #5dbcd2;
    font-weight: 900;
    text-decoration: none;
}
.HD-rightones {
    display:inline;
}
.Hd-active {
    font-weight: 900;
    color: #439eb3;
}
.chat-message {
    list-style-type: none;
    display: inline-flex;
    margin-bottom: 10px;
    align-content: center;
}
.chat-profilebox {
    justify-content: center;
    text-align: center;
    margin-right: 30px;
    padding: auto;
}
.chat-profilebox > p {
    font-size: 0.8em;
    font-weight: bold;
    margin-top: 5px;
    justify-content: center;
}
.chat-actionbox {
    display: flex;
    padding: auto;
    height: 50px;
    text-align: center;
    justify-content: center;
    font-weight: bold;
}
.chat-scriptbox {
    display: flex;
    padding: 15px 15px 0px 15px;
    height: 70px;
    justify-content: center;
    border-radius: 20%;
    border: 1px solid #999;
}
.editBtn {
    margin: auto;
    margin-left: 10px;
}
.writingroompage {
    height: 90%;
    width: 1000px;
    margin: 0;
}
.writingroom-title {
    width: 100%;
    display: inline;
    text-align: center;
    padding: 10px 0 0 10px;
    background-color: #6c757d;
}
.WR-title {
    font-weight: 700;
}
.WR-backtoMain {
    float: left;
    color: white;
    background-color: #6c757d;
    border: none;
    padding: 3px;
    margin-left: 5px;
}
.WR-link {
    color: white;
}
.WRdropdown-here {
    background-color: #6c757d;
    text-align: right;
}
.WRdropdownedMenu {
    font-size: 0.9em;
}
.dropdown-toggle::after {
    display:none;
}
.trytohandle {
    height: 100%;
}
.Chatcontainer {
    display: block;
    position: relative;
    margin-right: 0px;
}
.WR-MainArea {
    height: 560px;
    background-color: 1px solid #f1f1f1;
    padding: 20px;
    margin: 0;
    overflow: auto;
}
.WR-sidebar {
    list-style-type: none;
    margin: 0;
    padding: 0;
    background-color: #f1f1f1;
    position: fixed;
    height: 100%;
    overflow: auto;
    float: right;
  }
.Wr-sidebar-comp {
    display: block;
    color: #000;
    padding: 6px;
    text-decoration: none;
    text-align: center;
    font-size: 2.2em;
  }
.Wr-sidebar-comp > .WR-small {
    font-size: 16px;
}  
.Wr-sidebar-comp:hover {
    background-color: #555;
    color: white;
}
.WR-scriptbar {
    align-items: center;
    margin-top: 5px;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: rgb(168, 168, 168);
}
#userCharacterImg {
    height: 50px;
    width: 50px;
    border-radius: 50%;
}
#characterSelect {
    background-color: transparent;
    border: none;
    border-radius: 50%;
}
.scriptInput {
    padding: 10px;
    width: 80%;
    border-radius: 8px;
    border: none;
    margin-left: 6px;
    margin-right: 14px;
}
.WR-submitBtn {
    display: inline;
}
.WR-submitBtn > button {
    padding: 8px;
    background-color: white;
    border-radius: 8px;
    border: solid 1px gray;
}
.NV-li {
    float:right;
}
.right_align1{
    
    position: absolute;
    right: 0px;
    top: 0px;
    width: 50px;
    height:50px;
}
.container{
   display: table;
}

.vertical_menus{
    display: table-cell;
    vertical-align: right;
}
.vertical_icon{
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    right: 0;
}

.right_align1{
    
    position: absolute;
    right: 0px;
    top: 0px;
    width: 50px;
    height:50px;
}
.right_align2{
    position: absolute;
    right: 0px;
    top: 50px;
    width: 50px;
    height:50px;
}
.right_align3{
    position: absolute;
    right: 0px;
    top: 100px;
    width: 50px;
    height:50px;
}
.right_align4{
    position: absolute;
    right: 0px;
    top: 150px;
    width: 50px;
    height:50px;
}
.nav_loc{
    position: absolute;
    right: 0px;
}
.column{
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(1, 30px);
    grid-template-rows: repeat(4, 30px);
    position: relative;
}
.center{
    vertical-align: center;
}

.content {
    margin-top: 50px;
    border-radius: 20px;
    padding: 50px 50px 50px 50px;
    border: solid 1px gray;
}
.login-logo {
    font-size: 2em;
    font-weight: 900;
    color: #43afc7;
}
.formInput {
    border-radius: 8px;
    border: solid 1px gray;
    margin-left: 10px;
    margin-bottom: 10px;
    padding: 4px 15px 4px 15px;
    font-family: Roboto;
    font-size: 0.8em;
    width: 200px;
}
.submitBtn {
    border-radius: 10px;
    border: none;
    background-color: #43afc7;
    padding: 10px 30px 10px 30px;
    margin-left: 170px;
}

.linki {
    font-size: 1em;
}
